import { useNavigate } from 'react-router-dom';
import React, { useState } from 'react';
import EditorCode from "./EditorCode";
import StepButton from './StepButton';

export default function Redux() {
  const navigate = useNavigate();
  const [isAnimating, setIsAnimating] = useState(false);

  const handleNavigate = () => {
    setIsAnimating(true);
    setTimeout(() => {
      navigate('/main');
    }, 500); 
  };


const actions = `
const increment = () => {
  return {
    type: 'INCREMENT'
  };
};

const decrement = () => {
  return {
    type: 'DECREMENT'
  };
};
`;

const reducer = `
const initialState = {
  count: 0
};

const counterReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'INCREMENT':
      return {
        ...state,
        count: state.count + 1
      };
    case 'DECREMENT':
      return {
        ...state,
        count: state.count - 1
      };
    default:
      return state;
  }
};

export default counterReducer;
`;

const selector = `
import React from 'react';
import { useSelector } from 'react-redux';

const Counter = () => {
  const count = useSelector((state) => state.count);

  return <div>{count}</div>;
};

`;

const dispatch = `
import React from 'react';
import { useDispatch } from 'react-redux';

const Counter = () => {
  const dispatch = useDispatch();

  return (
    <div>
      <button onClick={() => dispatch({ type: 'INCREMENT' })}>+</button>
      <button onClick={() => dispatch({ type: 'DECREMENT' })}>-</button>
    </div>
  );
};

`;




    return (
      <div className={`reactPageContainer ${isAnimating ? 'fading' : ''}`}>
        <div className='reactReduxContainer'>
        <h1 className='Title'>Redux</h1>
        <hr />
        <span className='reactReduxDescription'>
        Redux to kontener stanu dla aplikacji JavaScript, ktry pomaga zarzdza stanem aplikacji w sposb przewidywalny i skalowalny. 
        Jest szczeglnie przydatny w duych aplikacjach, gdzie zarzdzanie stanem moe sta si skomplikowane.
        </span>
        <span className='reactReduxIntro'>
        Gwne koncepcje Redux:
        </span>
        <span className='reactReduxReasons'><b>1</b>Store: Jednordowe drzewo stanu aplikacji.</span>
        <span className='reactReduxReasons'><b>2</b>Actions: Obiekty, ktre opisuj co si stao.</span>
        <span className='reactReduxReasons'><b>3</b>Reducers: Funkcje, ktre okrelaj jak stan aplikacji zmienia si w odpowiedzi na akcje.</span>
        <span className='reactReduxReasons'><b>4</b>Dispatch: Funkcja do wysyania akcji do store.</span>
        <span className='reactReduxReasons'><b>5</b>Provider: Komponent React, ktry udostpnia store aplikacji.</span>
        <span className='reactReduxReasons'><b>6</b>Hooks (useSelector, useDispatch): Hooki do czenia komponentw React z Reduxem.</span>
        </div>

        <div className='reactReduxContainer'>
          <h1 className='reactHooksTitle'>Store</h1>
          <span className='reactReduxDescription'>
          Store jest pojedynczym obiektem, ktry przechowuje stan aplikacji. 
          Store jest tworzony przy uyciu funkcji createStore.
          </span>
        </div>

        <div className='reactReduxContainer'>
          <h1 className='reactHooksTitle'>Actions</h1>
          <span className='reactReduxDescription'>
          Actions to obiekty, ktre opisuj co si stao w aplikacji. 
          Kada akcja musi mie przynajmniej typ (type), ktry jest zazwyczaj stringiem.
          </span>
          <div className='reackHooksEditor'>
          <EditorCode string={actions}/>
          </div>
        </div>

        <div className='reactReduxContainer'>
          <h1 className='reactHooksTitle'>Reducers</h1>
          <span className='reactReduxDescription'>
          Reducers to funkcje, ktre okrelaj jak stan aplikacji zmienia si w odpowiedzi na akcje.
          Reducer przyjmuje dwa argumenty: aktualny stan i akcj, i zwraca nowy stan.
          </span>
          <div className='reackHooksEditor'>
          <EditorCode string={reducer}/>
          </div>
        </div>

        <div className='reactReduxContainer'>
          <h1 className='reactHooksTitle'>Dispatch</h1>
          <span className='reactReduxDescription'>
          Dispatch to metoda store, ktra suy do wysyania akcji do store.
          Wywoanie dispatch powoduje, e wszystkie reducetry s wywoywane z przekazan akcj.
          </span>
        </div>

        <div className='reactReduxContainer'>
          <h1 className='reactHooksTitle'>Synchronizacja danych</h1>
          <span className='reactReduxDescription'>
          Redux utrzymuje jednolity stan aplikacji, co uatwia synchronizacj danych midzy komponentami. 
          Gdy stan si zmienia, odpowiednie komponenty s ponownie renderowane
          </span>
        </div>

        <div className='reactReduxContainer'>
          <h1 className='reactHooksTitle'>useSelector | useDispatch</h1>
          <span className='reactReduxDescription'>
          React Redux oferuje hooki do czenia komponentw z Reduxem. 
          Hook useSelector wybiera dane ze store. Hook useDispatch zwraca funkcj dispatch z Redux store.
          </span>
          <div className='reackHooksEditor'>
          <EditorCode string={selector}/>
          <EditorCode string={dispatch}/>
          </div>
        </div>

        <div className='reactReduxContainer'>
          <h1 className='reactHooksTitle'>Provider</h1>
          <span className='reactReduxDescription'>
          Provider to komponent, ktry dostarcza Redux store wszystkim komponentom w aplikacji. 
          Jest to konieczne, aby uywa hookw takich jak useSelector i useDispatch.
          </span>
          <div className='reackHooksEditor'>
          <EditorCode string={selector}/>
          <EditorCode string={dispatch}/>
          </div>
          <span className='reactReduxDescription'>
          Redux zapewnia przewidywalny sposb zarzdzania stanem aplikacji poprzez koncepcje takie jak store, actions, reducers i dispatch. 
          Integracja z Reactem za pomoc Provider oraz hookw useSelector i useDispatch pozwala na atwe czenie stanu aplikacji z komponentami Reacta, zapewniajc skalowalno i prostot w zarzdzaniu stanem w duych aplikacjach.
          </span>
        </div>

        <StepButton handleClick={handleNavigate}/>
      </div>
    );
  }
  

  