import React, {useRef} from 'react';

export default function Analiza() {

  const sectionRef = useRef(null);


    const handleButtonClick = () => {
      sectionRef.current.scrollIntoView({ behavior: 'smooth' });
    };


  return (
    <div className='reactMtodykaContiner'>
      <div className='reactReduxContainer' ref={sectionRef}>
        <h1 className='Title'>DevOps vs DevSecOps</h1>
        <hr />
        <span className='reactReduxDescription'>
        DevOps to zestaw praktyk, ktre cz programowanie oprogramowania (Dev) i operacje IT (Ops), aby poprawi wspprac, komunikacj i szybko dostarczania oprogramowania. Celem DevOps jest redukcja czasu, jaki potrzebny jest na dostarczenie aktualizacji oprogramowania i funkcji klientom, a take poprawa jakoci i niezawodnoci tych aktualizacji.
        DevSecOps, odwrotnie, jest rozszerzeniem DevOps, ktre integruje praktyki i zasady bezpieczestwa w caym cyklu ycia rozwoju oprogramowania. Celem DevSecOps jest zapewnienie, aby bezpieczestwo byo integraln czci kadego etapu rozwoju, od planowania do wdroenia, aby zmniejszy ryzyko wadanoci i narusze zgodnoci.
        </span>
        <h1 className='reactHooksTitle'>Gwne rnice</h1>
        <span className='reactReduxReasons'><b>1</b>DevOps koncentruje si na poprawie szybkoci i jakoci dostarczania oprogramowania, podczas gdy DevSecOps koncentruje si na zapewnieniu bezpieczestwa i zgodnoci dostarczania oprogramowania</span>
        <span className='reactReduxReasons'><b>2</b>DevOps obejmuje szerszy zakres osb, w tym programistw, inynierw testowych i zesp operacyjny. DevSecOps obejmuje szerszy zakres osb, w tym zesp bezpieczestwa, programistw i zesp operacyjny</span>
        <span className='reactReduxReasons'><b>3</b>DevOps niekoniecznie obejmuje bezpieczestwo jako integraln cz swoich praktyk. DevSecOps, odwrotnie, integruje bezpieczestwo jako krytyczn cz swoich praktyk</span> 
      </div>

      <div className='reactReduxContainer'>
        <h1 className='reactHooksTitle'>Wady, ktre mog naprawi</h1>
        <span className='reactReduxReasons'><b>1</b>DevOps i DevSecOps mog pomc mostowa luk w komunikacji midzy zespoem programistycznym a zespoem operacyjnym poprzez zapewnienie, e wszyscy uczestnicy s wiadomi zmian i potencjalnych problemw</span>
        <span className='reactReduxReasons'><b>2</b>DevSecOps moe pomc identyfikacji i naprawie niesprawnych praktyk programistycznych poprzez integracj testowania bezpieczestwa i skanowania luk w celu identyfikacji wadanoci</span>
        <span className='reactReduxReasons'><b>3</b>DevOps i DevSecOps mog pomc identyfikacji braku testowania poprzez zapewnienie, e wszystkie aspekty dostarczania oprogramowania s dokadnie testowane</span> 
        <span className='reactReduxReasons'><b>4</b>DevSecOps moe pomc identyfikacji i naprawie niesprawnych konfiguracji poprzez integracj zarzdzania konfiguracj bezpieczestwa w celu identyfikacji luk w konfiguracji</span>
        <span className='reactReduxReasons'><b>5</b>DevSecOps moe pomc identyfikacji braku monitorowania poprzez zapewnienie, e wszystkie aspekty dostarczania oprogramowania s monitorowane w celu wykrycia problemw bezpieczestwa i wydajnoci</span> 
      </div>

      <div className='reactReduxContainer'>
        <h1 className='reactHooksTitle'>Jak mog je naprawi</h1>
        <span className='reactReduxReasons'><b>1</b>Implementacja cigej integracji i cigego wdroenia (CI/CD): CI/CD pipeline mog automatyzowa testowanie, walidacj i wdroenie aktualizacji oprogramowania, redukujc ryzyko bdw ludzkich</span>
        <span className='reactReduxReasons'><b>2</b>Integracja testowania bezpieczestwa: Integracja narzdzi testowych bezpieczestwa w pipeline CI/CD moe pomc wykry wadanoci wczeniej w cykl rozwoju</span>
        <span className='reactReduxReasons'><b>3</b>Uycie niesprawnych praktyk programistycznych: Implementacja niesprawnych praktyk programistycznych, takich jakwalidacja wejciowych danych, obsuga bdw i bezpieczne magazyny danych</span> 
        <span className='reactReduxReasons'><b>4</b>Konfiguracja ustawie bezpieczestwa: Konfiguracja ustawie bezpieczestwa, takich jakreguy firewalla, kontrola dostpu i szyfrowanie, aby zapewni sprawne konfiguracje</span>
        <span className='reactReduxReasons'><b>5</b>Monitorowanie problemw bezpieczestwa: Implementacja narzdzi monitorowania problemw bezpieczestwa, takich jak skanery luk i systemy wykrywania intruzji, aby wykry problemy bezpieczestwa w czasie rzeczywistym</span> 
        <span className='reactReduxReasons'><b>6</b>Rozwj planu odpowiedzi na incydenty: Rozwj planu odpowiedzi na incydenty aby szybko reagowa na incydenty bezpieczestwa i minimalizowa ich wpyw</span>
        <span className='reactReduxReasons'><b>7</b>Pomalanie szkolenia i edukacji: Pomalanie szkolenia i edukacji dla programistw na temat niesprawnych praktyk programistycznych i najlepszych praktykach bezpieczestwa</span> 
      </div>

      <button onClick={handleButtonClick} className='StepButton'>Do gry</button>
    </div>
  );
}


