import { useNavigate } from 'react-router-dom';
import React, { useState } from 'react';
import EditorCode from "./EditorCode";
import StepButton from './StepButton';

export default function ReactPage() {
  const navigate = useNavigate();
  const [isAnimating, setIsAnimating] = useState(false);

  const handleNavigate = () => {
    setIsAnimating(true);
    setTimeout(() => {
      navigate('/main');
    }, 500); 
  };

  const imperative = `
  const element = document.createElement('div');
  element.textContent = 'Hello, World!';
  document.body.appendChild(element);
  `;

  const declarative = `
  const App = () => {
    return (
      <div>
        Hello, World!
      </div>
    );
  }  
  `;

  const prop = `
  const Greeting = (props) => {
    return <h1>Hello, {props.name}!</h1>;
  }
  
  const App = () => {
    return <Greeting name="World" />;
  }
  
  `;


    return (
      <div className={`reactPageContainer ${isAnimating ? 'fading' : ''}`}>
        <div className='reactPageIntro'>

          <h1>React</h1>
          <hr />
          <span className='reactPageIntroFirstSpan'>React to biblioteka JavaScript do budowania interfejsw uytkownika, stworzona przez Facebooka w 2013 roku. Jest uywana gwnie do tworzenia single page applications, 
            gdzie treci s dynamicznie adowane i aktualizowane bez koniecznoci odwieania caej strony. React skupia si na komponentach, co umoliwia tworzenie zoonych i interaktywnych interfejsw uytkownika z mniejszych, wielokrotnego uytku blokw kodu.
          </span>
        </div>

        <div className='reactPageReasons'>
          <h1>Dlaczego React?</h1>
          <hr/>
          <span className='reactPageIntroSecondSpan'>
            React jest wybierany z wielu powodw:
          </span>

            <span className='reactPageReasonCount'><b>1</b> Komponentowo: Pozwala tworzy wielokrotnego uytku komponenty, ktre mog by atwo czone, aby tworzy zoone aplikacje.</span>
            <span className='reactPageReasonCount'><b>2</b> Virtual DOM: React uywa wirtualnego DOM (Document Object Model), ktry poprawia wydajno aplikacji poprzez minimalizacj bezporednich manipulacji rzeczywistym DOM.</span>
            <span className='reactPageReasonCount'><b>3</b> Spoeczno i Ekosystem: React ma du spoeczno oraz wsparcie, co oznacza dostpno wielu narzdzi, bibliotek i zasobw.</span>
            <span className='reactPageReasonCount'><b>4</b> JSX: React uywa JSX, czyli rozszerzenia skadni JavaScript, ktre pozwala pisa kod HTML w JavaScript, co uatwia tworzenie komponentw.</span>
            <span className='reactPageReasonCount'><b>5</b> Deklaratywno: React pozwala opisywa interfejs uytkownika w sposb deklaratywny, co uatwia zrozumienie i utrzymanie kodu.</span>
          
            <span className='reactPageTypeTitle'>
              Imperatywno | Deklaratywno
            </span>

            <div className='reactPageEditorImpAndDec'>
              <EditorCode string={imperative}/>
              <EditorCode string={declarative}/>
            </div>

            <span className='reactPageImperative'>
              Imperatywno: Styl programowania, ktry polega na dokadnym opisywaniu krok po kroku, jak co ma by zrobione. 
              Przykadem moe by klasyczne manipulowanie DOM za pomoc czystego JavaScript'u
            </span>

            <span className='reactPageDeclartive'>
              Deklaratywno: Styl programowania, ktry skupia si na opisywaniu, co ma by zrobione, a nie jak. 
              React jest przykadem deklaratywnego podejcia
            </span>

          </div>
          

        <div className='reactPageComponents'>
          <h1>Komponenty</h1>
          <hr />
          <span className='reactPageUITitle'>
          Zarzdzanie UI
          </span>

          <span className='reactPageUIDescription'>
          W React zarzdzanie interfejsem uytkownika odbywa si za pomoc komponentw. 
          Kady komponent moe zarzdza swoim wasnym stanem i renderowa rne elementy w zalenoci od tego stanu. 
          React automatycznie aktualizuje interfejs uytkownika, gdy stan komponentw si zmienia. 
          Mona te uywa narzdzi takich jak Redux lub Context API do zarzdzania stanem na poziomie caej aplikacji
          </span>

          <span className='reactPageSendingInfo'>
          Przesyanie danych
          </span>

          <span className='reactPageSendingInfoDescription'>
          Komponenty to podstawowe elementy w React, ktre mog by klasowe lub funkcyjne. 
          Kady komponent moe posiada wasny stan i cykl ycia. 
          Dane mog by przesyane midzy komponentami za pomoc propsw.
          </span>

          <div className='reactPageUIEditor'>
            <EditorCode string={prop} />
          </div>

          
        </div>

        <div className='reactPageJSX'>
          <h1>JSX</h1>
          <hr />
          <span className='reactPageJSXDescription'>
          JSX (JavaScript XML) to skadnia pozwalajca na pisanie HTML w ramach JavaScript. JSX upraszcza tworzenie komponentw React, umoliwiajc czenie logiki z markowaniem HTML w bardziej przejrzysty sposb. 
          JSX jest nastpnie transpilowany do wywoa funkcji React.createElement.
          </span>
        </div>
        <StepButton handleClick={handleNavigate}/>
      </div>
    );
  }
  
 
  