import React, {useRef} from 'react';

export default function Metodyka() {

  
    const sectionRef = useRef(null);


    const handleButtonClick = () => {
      sectionRef.current.scrollIntoView({ behavior: 'smooth' });
    };

  return (
    <div className='reactMtodykaContiner'>
      
      <div className='reactReduxContainer' ref={sectionRef}>
        <h1 className='Title'>Redux</h1>
        <hr />
        <span className='reactReduxDescription'>
        Redux to kontener stanu dla aplikacji JavaScript, ktry pomaga zarzdza stanem aplikacji w sposb przewidywalny i skalowalny. 
        Jest szczeglnie przydatny w duych aplikacjach, gdzie zarzdzanie stanem moe sta si skomplikowane.
        </span>
        <h1 className='reactHooksTitle'>Planowanie i Projektowanie</h1>
        <span className='reactReduxDescription'>
        Zanim zagbimy si w proces tworzenia strony internetowej, wane jest dokadne jej zaplanowanie i zaprojektowanie. Dobrze przemylany plan pomoe nam upewni si, e nasza strona spenia swj zamierzony cel i skutecznie komunikuje si z grup docelow. W tym temacie bdziemy zgbia znaczenie planowania i projektowania strony internetowej oraz jak to ustawia scen dla udanej obecnoci online.
        </span>
        <span className='reactReduxReasons'><b>1</b>Zdefiniuj cel strony, swoj publiczno i cele</span>
        <span className='reactReduxReasons'><b>2</b>Zbadaj konkurencj, analizuj zachowania publicznoci i identyfikuj unikatowe atuty strony</span>
        <span className='reactReduxReasons'><b>3</b>Stwrz map strony, aby zilustrowa struktur i organizacj strony</span>
        <span className='reactReduxReasons'><b>4</b>Stwrz wireframe, aby zilustrowa ukad i interfejs uytkownika</span>
      </div>

      <div className='reactReduxContainer'>
        <h1 className='reactHooksTitle'>Kolory</h1>
        <span className='reactReduxDescription'>
        Kolory odgrywaj istotn rol w naszym codziennym yciu, a ich wpyw jest szczeglnie widoczny w wiecie cyfrowym. Dobrze dobrany schemat kolorw moe wywoa emocje, przekazywa znaczenie, a nawet wpywa na zachowanie uytkownikw. W tym temacie zgbimy wiat teorii kolorw, eksplorujc, jak kolory mog by wykorzystane do stworzenia spjnej i angaujcej tosamoci wizualnej naszej strony.
        </span>
        <span className='reactReduxReasons'><b>1</b>Zrozumie podstawy teorii kolorw, w tym harmoni kolorw, kontrast i psychologi</span>
        <span className='reactReduxReasons'><b>2</b>Wybierz schemat kolorw, ktry pasuje do celu strony i publicznoci</span>
        <span className='reactReduxReasons'><b>3</b>Uyj kolorw do przekazywania znacze, takich jak akcentowanie dziaa powracajcych lub podkrelanie kluczowych wiadomoci</span>
        <span className='reactReduxReasons'><b>4</b>Ubezpiecz swoje kolorystyk, aby stworzy spjny wygld i odczucie</span>

        <h1 className='reactHooksTitle'>Znaczenie kolorw</h1>
        <span className='reactReduxReasons'><b>1</b>Czerwony - energia, akcja</span>
        <span className='reactReduxReasons'><b>2</b>Zielony - natura, stabilno</span>
        <span className='reactReduxReasons'><b>3</b>ty - optimism, arliwo</span>
        <span className='reactReduxReasons'><b>4</b>Niebieski - spokojno, wiara</span>
        <span className='reactReduxReasons'><b>5</b>Fioletowy - kreatywno, sztuka</span>
        <span className='reactReduxReasons'><b>6</b>Pomaraczowy - kreatywno, innowacyjno</span>
        <span className='reactReduxReasons'><b>7</b>Biay - czysto, neutralno</span>
        <span className='reactReduxReasons'><b>8</b>Szary - neutralno, stabilno</span>
      </div>


      <div className='reactReduxContainer'>
        <h1 className='reactHooksTitle'>Ukad</h1>
        <span className='reactReduxDescription'>
        Ukad strony ma kluczowe znaczenie dla jej oglnego sukcesu, poniewa okrela, jak uytkownicy poruszaj si i wchodz w interakcje z treci. Dobrze zaprojektowany ukad moe sprawi, e strona bdzie intuicyjna, atwa w uyciu i estetycznie atrakcyjna, podczas gdy le zaprojektowany ukad moe prowadzi do frustracji i dezorientacji. W tym temacie bdziemy bada zasady dobrego projektowania ukadu i eksplorowa sposoby tworzenia przyjaznej dla uytkownikw i estetycznie przyjemnej strony.
        </span>
        <span className='reactReduxReasons'><b>1</b>Uyj systemw siatek, aby stworzy struktur ukadu i poprawi hierarchi wizualn</span>
        <span className='reactReduxReasons'><b>2</b>Organizuj treci za pomoc jasnej hierarchii,prioritujc kluczowe informacje i tworzc spjny wizualny przepyw</span>
        <span className='reactReduxReasons'><b>3</b>Uyj pustoci, aby stworzy czysty i niezakcony design</span>
        <span className='reactReduxReasons'><b>4</b>Wybierz typografi, ktra pasuje do celu strony i publicznoci, dbajc o czytelno</span>
      </div>

      <div className='reactReduxContainer'>
        <h1 className='reactHooksTitle'>Responsywsno i projektowanie</h1>
        <span className='reactReduxDescription'>
        W dzisiejszym cyfrowym krajobrazie istotne jest, aby zapewni, e nasze strony internetowe s dostpne i uyteczne na rnych urzdzeniach i rozmiarach ekranu. Projektowanie responsywne jest kluczowym elementem nowoczesnego rozwoju sieci, pozwalajc nam tworzy strony, ktre bezproblemowo dostosowuj si do rnych urzdze i rozmiarw ekranw. W tym temacie zgbimy znaczenie projektowania responsywnego i dowiemy si, jak zaimplementowa go w naszych wasnych projektach.
        </span>
        <span className='reactReduxReasons'><b>1</b>Projektuj dla rnych urzdze i rozdzielczoci ekranu za pomoc principw projektowania odpowiadajcego</span>
        <span className='reactReduxReasons'><b>2</b>Prioritetuj design mobilny, aby zapewni atwe korzystanie ze strony na mniejszych ekranach</span>
        <span className='reactReduxReasons'><b>3</b>Definiuj przypadki, aby zoptymalizowa design dla rnych rozdzielczoci ekranu i urzdze</span>
        <span className='reactReduxReasons'><b>4</b>Testuj odpowiadanie strony na rnych urzdzeniach, aby zapewni spjny wizualny wypyw</span>
      </div>

      <div className='reactReduxContainer'>
        <h1 className='reactHooksTitle'>Responsywsno i projektowanie</h1>
        <span className='reactReduxDescription'>
        Tre stanowi podstaw kadej udanej strony internetowej, dostarczajc uytkownikom cennej informacji, rozrywki lub obu. W tym temacie omwimy znaczenie tworzenia wysokiej jakoci treci, ktra rezonuje z nasz grup docelow, oraz eksplorowa strategie organizowania i prezentowania treci w sposb angaujcy uytkownikw i zachcajcy ich do dziaania.
        </span>
        <span className='reactReduxReasons'><b>1</b>Rozwijaj strategi treci, ktra pasuje do celu strony i publicznoci</span>
        <span className='reactReduxReasons'><b>2</b>Pisz wysokiej jakoci treci, ktre budz zainteresowanie publicznoci</span>
        <span className='reactReduxReasons'><b>3</b>Organizuj treci za pomoc jasnych nagwkw i podnagwkw oraz metaopisw</span>
        <span className='reactReduxReasons'><b>4</b>Uyj wysokiej jakoci wizualnych elementw, takich jak zdjcia i filmy, aby poprawi wizualny wypyw</span>
      </div>

      <button onClick={handleButtonClick} className='StepButton'>Do gry</button>
    </div>
  );
}


