import { useNavigate } from 'react-router-dom';
import React, { useState } from 'react';
import EditorCode from "./EditorCode";
import StepButton from './StepButton';


export default function Styling() {
  const navigate = useNavigate();
  const [isAnimating, setIsAnimating] = useState(false);

  const handleNavigate = () => {
    setIsAnimating(true);
    setTimeout(() => {
      navigate('/main');
    }, 500); 
  };

  const sass = `
  $primary-color: #3498db;

.container {
  color: $primary-color;
  .header {
    font-size: 2rem;
  }
}
  `;

  const tailwind = `
  const App = () => {
    return (
      <div className="p-4 bg-blue-500 text-white">
        Hello, Tailwind CSS!
      </div>
    );
  }  
  `;

  const ui = `
  import React from 'react';
  import { Button } from '@mui/material';

  const App = () => {
    return (
      <Button variant="contained" color="primary">
        Hello, Material-UI!
      </Button>
    );
  }

  export default App;
  `;


    return (
      <div className={`reactPageContainer ${isAnimating ? 'fading' : ''}`}>

        <div className='reactStylingContainer'>
          <h1>Styling</h1>
          <hr />
          <span className='reactStylingSpan'>
          Stylowanie w React moe by realizowane na wiele rnych sposobw. 
          Mona korzysta z tradycyjnych metod takich jak pliki CSS, 
          jak i bardziej zaawansowanych rozwiza jak SCSS, Tailwind CSS, a take gotowych komponentw z otwartego rda. 
          Dodatkowo react pozwala na szybkie dodawanie className do elementw strony.
          </span>

          <div id='style2' className='reactStylingContainer'>
            <h1>Scss i Tailwind</h1>
            <hr />
            <span className='reactStylingTitle'>SCSS</span>
            <span className='reactStylingSpan'>
            SCSS (Sassy CSS) to rozszerzenie CSS, ktre wprowadza funkcjonalnoci takie jak zmienne, 
            zagniedanie, dziedziczenie i funkcje. SCSS moe by uywany w projektach React dziki narzdziom takim jak node-sass lub sass
            </span>
            <div className='reactStylingEditor'>
              <EditorCode string={sass}/>
            </div>
          </div>

          <div className='reactStylingContainer'>
            <span className='reactStylingTitle'>TAILWIND</span>
            <span className='reactStylingSpan'>
            Tailwind CSS to framework do stylowania, ktry oferuje gotowe klasy pomocnicze, 
            pozwalajce na szybkie tworzenie nowoczesnych interfejsw bez koniecznoci pisania niestandardowego CSS. 
            Tailwind jest bardzo popularny dziki swojej elastycznoci i moliwoci dostosowania do potrzeb projektu.
            </span>
            <div className='reactStylingEditor'>
              <EditorCode string={tailwind}/>
            </div>
          </div>

          <div className='reactStylingOpenSource'>
            <h1>Open source</h1>
            <hr />
            <span className='reactStylingSpan'>
            Istnieje wiele gotowych komponentw open source, ktre mona wykorzysta w projektach React. 
            Komponenty te czsto s stylowane i zawieraj rne funkcjonalnoci. 
            React oferuje szeroki ekosystem bibliotek i komponentw open source, 
            ktre mona wykorzysta do przyspieszenia procesu tworzenia aplikacji. 
            Jedn z przykadowych bibliotek komponentw jest Material-UI inspirowana zasadami Material Design od Google. 
            Oferuje szeroki wybr gotowych, responsywnych komponentw, ktre mona atwo dostosowa do wasnych potrzeb. 
            Natomiast przykadow bibliotek do animacji, ktra jest take wykorzystywana w niektrych miejscach w tej aplikacji to React Transition Group.
            </span>
            <div className='reactStylingEditor'>
              <EditorCode string={ui}/>
            </div>
          </div>

        </div>
        
        <StepButton handleClick={handleNavigate}/>
      </div>
    );
  }
  
 
  